import React from 'react'
import {render} from 'react-dom'
import {Provider} from 'react-redux'
import 'isomorphic-fetch'

import App from "./components/App";
import AppNavbar from './components/AppNavbar'
import IpamTable from './components/IpamTable'
import './components/fontAwesome/faLibrary.js'
import configureStore from './configureStore'

const store = configureStore()
//debug only
window.store = store
window.start = Date.now()
window.profile = {}
window.logger = (message, data = {}) => {
    console.log(message, ': ', Date.now() - window.start, {...data})
    window.start = Date.now()
}
window.profiler = (message) => {
    const time = Date.now() - window.start
    window.profile[message] = typeof window.profile[message] === "undefined" ? 0 : window.profile[message] + time
    window.start = Date.now()
}

render(
    <Provider store={store}>
        <App>
            <App.Header>
                <AppNavbar />
            </App.Header>
            <App.Body>
                <IpamTable/>
            </App.Body>
            <App.Footer>

            </App.Footer>
        </App>
    </Provider>,
    document.getElementById("root-container"));