import React, {Component} from 'react'
import PropTypes from 'prop-types'
import check from 'check-types'
import {tableConnect} from '../TableContext'
import Column from '../Column'

class Row extends Component {
    innerStyles = () => {
        const {jssSheet: {classes: css}} = this.props.tableContext
        const {isHeader, isFooter} = this.props
        if (!isHeader && !isFooter) return [css.tableBodyRowDc]
        if (isHeader) return [css.tableHeaderRowDc]
        if (isFooter) return [css.tableFooterRowDc]
        return []

    }

    render() {
        const {isHeader, isFooter, rowRef: createRef, rowId} = this.props
        const injectedProps = isHeader ? {isHeader: true, } : (isFooter ? {isFooter: true, } : {})
        const {tableContext, tableContext: {filterComponentsByType, joinCss, jssSheet: {classes: css}, createRowRef}} = this.props

        return (
            <tr className={joinCss(this.innerStyles(), this.props.cssClasses).join(" ")} ref={rowId ? createRowRef(rowId) : null}>
                {filterComponentsByType(this.props.children, Column, injectedProps)}
                {isHeader || isFooter ? <td className={css.scrollSz} /> : <td className={css.scrollBodySz} />}
            </tr>
        )
    }
}

Row.propTypes = {
    cssClasses: PropTypes.oneOfType([
        PropTypes.array,
        PropTypes.string
    ]), //custom css classes
    rowId: PropTypes.any //if exists - invoke createRowRef to create ref
}
Row.defaultProps = {}

export default tableConnect(Row)
